import { Formik, FormikActions, FormikProps } from 'formik';
import {
  classNamesFunction,
  CommandBar,
  css,
  DefaultButton,
  IContextualMenuItem,
  IPanelProps,
  IPanelStyleProps,
  IPanelStyles,
  Panel,
  PanelType,
  PrimaryButton
} from 'office-ui-fabric-react';
import { withResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { isNullOrUndefined, isUndefined } from 'util';
import { EmptyFilterSearchRA } from '../../../models/aadPAP/roleAssignment/IFilterSearchRA';
import { EMPTY_ROLE_DEFINITION_WITH_LOADING } from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { ISelectedRoleDefinition } from '../../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { CUSTOMIZED_PANEL_STYLE } from '../../../Styles/PANEL_STYLES_RD_RA';
import { newGuid } from '../../../utilities/guid';
import { AuthzPanelType } from '../RoleDefinition.types';
import { ConnectedRoleDefinitionForm } from '../RoleDefinitionForm/ConnectedRoleDefinitionForm';
import { RoleDefinitionFormValue } from '../RoleDefinitionForm/RoleDefinitionFormValue';
import { getClassNames } from './RoleDefinitionPanel.classNames';
import {
  FormPanelType,
  IRoleDefinitionPanelProps,
  IRoleDefinitionPanelState
} from './RoleDefinitionPanel.type';

export const getPanelClassNames = classNamesFunction<
  IPanelStyleProps,
  IPanelStyles
>();

@withResponsiveMode
export class RoleDefinitionPanel extends React.Component<
  IRoleDefinitionPanelProps,
  IRoleDefinitionPanelState
> {
  constructor(props: IRoleDefinitionPanelProps) {
    super(props);
    this.state = {
      isEditDisabled: props.roleDefinitionPanelType !== FormPanelType.new
    };
  }
  public render(): React.ReactNode {
    const { roleDefinitionPanelType } = this.props;
    let selectedRoleDefinition: ISelectedRoleDefinition;
    if (roleDefinitionPanelType === FormPanelType.new) {
      selectedRoleDefinition = { ...EMPTY_ROLE_DEFINITION_WITH_LOADING };
      selectedRoleDefinition.id = newGuid();
    } else {
      selectedRoleDefinition = this.props.selectedRoleDefinition;
    }

    let panel = (
      <div>
        <Formik
          initialValues={new RoleDefinitionFormValue(selectedRoleDefinition)}
          validationSchema={RoleDefinitionFormValue.validationSchema}
          enableReinitialize
          onSubmit={this._handleSubmit}
          render={this._renderForm}
        />
      </div>
    );
    return panel;
  }

  private _renderForm = (
    roleDefinitionFormikProps: FormikProps<RoleDefinitionFormValue>
  ): React.ReactNode | undefined => {
    const { showPanel, roleDefinitionPanelType, showRAPanel } = this.props;
    const { isEditDisabled } = this.state;
    return (
      <Panel
        isOpen={showPanel}
        onDismiss={this._handleDismiss}
        isLightDismiss={true}
        type={PanelType.medium}
        headerText="Role Definition"
        styles={CUSTOMIZED_PANEL_STYLE}
        closeButtonAriaLabel="Close"
        onRenderHeader={this._renderHeader}
        onRenderFooterContent={() =>
          this._onRenderFooter(roleDefinitionFormikProps)
        }
      >
        <ConnectedRoleDefinitionForm
          roleDefinitionPanelType={roleDefinitionPanelType}
          // onUpdate={this.updateRoleDefinition}
          // onDelete={this.deleteRoleDefinition}
          onChangeAssignedUsers={this._changeAssignedUsers}
          togglePanel={showRAPanel}
          roleDefinitionFormikProps={roleDefinitionFormikProps}
          isEditDisabled={isEditDisabled}
        />
      </Panel>
    );
  }

  private _handleDismiss = () => {
    const { onDismiss, roleDefinitionPanelType } = this.props;
    onDismiss();
    roleDefinitionPanelType !== FormPanelType.new
      ? this.setState({ isEditDisabled: true })
      : undefined;
  }

  private _handleSubmit = (
    values: RoleDefinitionFormValue,
    actions: FormikActions<RoleDefinitionFormValue>
  ) => {
    this._updateRoleDefinition(values);
    actions.setSubmitting(true);
    this.setState({ isEditDisabled: true });
  }
  private _renderHeader = (
    props: IPanelProps | undefined,
    _defaultRender?: (props?: IPanelProps) => JSX.Element | null
  ): JSX.Element | null => {
    const {
      headerText,
      componentId,
      headerClassName = '',
      styles,
      theme
    } = props!;
    const classNames = getClassNames();
    const panelClassNames = getPanelClassNames(styles, { theme: theme! });
    const { roleDefinitionPanelType } = this.props;

    if (headerText) {
      return (
        <React.Fragment>
          <div
            className={css(
              'ms-Panel-header',
              panelClassNames.header,
              classNames.header
            )}
          >
            <p
              className={css(
                'ms-Panel-headerText',
                panelClassNames.headerText,
                classNames.headerText,
                headerClassName
              )}
              id={componentId + '-headerText'}
              role="heading"
              aria-level={1}
            >
              {headerText}
            </p>
          </div>
          {roleDefinitionPanelType !== FormPanelType.new ? (
            <div
              className={css(
                'ms-DetailsListHeaderActions'
                // detailPanelStyles.commandBar
              )}
            >
              <CommandBar
                items={this._getCommandBarItems()}
                ariaLabel={'Role Definition Form commands'}
              />
            </div>
          ) : (
            undefined
          )}
        </React.Fragment>
      );
    }
    return null;
  }

  private _onRenderFooter = (
    formikProps: FormikProps<RoleDefinitionFormValue>
  ): JSX.Element => {
    const { isEditDisabled } = this.state;
    const { roleDefinitionPanelType } = this.props;
    return (
      <div>
        <PrimaryButton
          type="submit"
          onClick={formikProps.submitForm}
          disabled={
            roleDefinitionPanelType !== FormPanelType.new
              ? formikProps.isSubmitting ||
                !formikProps.dirty ||
                !formikProps.isValid ||
                isEditDisabled
              : formikProps.isSubmitting ||
                !formikProps.dirty ||
                !formikProps.isValid
          }
        >
          Submit
        </PrimaryButton>
        &nbsp;
        <DefaultButton
          type="button"
          onClick={() => formikProps.resetForm()}
          disabled={
            roleDefinitionPanelType !== FormPanelType.new
              ? formikProps.isSubmitting || !formikProps.dirty || isEditDisabled
              : formikProps.isSubmitting || !formikProps.dirty
          }
        >
          Reset
        </DefaultButton>
      </div>
    );
  }

  private _getCommandBarItems = (): IContextualMenuItem[] => {
    const { roleDefinitionPanelType, selectedRoleDefinition } = this.props;
    return roleDefinitionPanelType !== FormPanelType.new
      ? [
          {
            key: 'delete',
            name: 'Delete',
            iconProps: {
              iconName: 'Delete'
            },
            ariaLabel: 'Delete Role Definition',
            onClick: () => {
              this._deleteRoleDefinition(selectedRoleDefinition);
            }
          },
          {
            key: 'edit',
            name: 'Edit',
            iconProps: {
              iconName: 'Edit'
            },
            onClick: () => this.setState({ isEditDisabled: false })
          },
          {
            key: 'assignPrincipal',
            name: 'Assign Principal',
            iconProps: {
              iconName: 'Assign'
            },
            onClick: this._onAssignOnClick
          }
        ]
      : [];
  }

  private _onAssignOnClick = () => {
    this.props.onDismiss();
    this.props.showRAPanel(
      AuthzPanelType.RoleAssignment,
      FormPanelType.assign,
      true
    );
  }
  private _deleteRoleDefinition = (
    roleDefinition: ISelectedRoleDefinition
  ): void => {
    this.props.deleteRoleDefinition(roleDefinition);
    if (!roleDefinition.isLoading) {
      this.props.onDismiss!();
    }
  }
  private _updateRoleDefinition = (
    roleDefinition: ISelectedRoleDefinition
  ): void => {
    this.props.updateRoleDefinition(roleDefinition);
    if (!roleDefinition.isLoading) {
      this.props.onDismiss!();
    }
  }

  private _changeAssignedUsers = (): void => {
    if (isNullOrUndefined(this.props.currentNpd.appId)) {
      return;
    }
    let roleAssignmentQueryParams = { ...this.props.roleAssignmentQueryParams };
    let filter = { ...EmptyFilterSearchRA };
    if (isUndefined(roleAssignmentQueryParams.filterSearchRA)) {
      roleAssignmentQueryParams.filterSearchRA = filter;
    }
    roleAssignmentQueryParams.appId = this.props.currentNpd.appId;
    if (this.props.roleDefinitionPanelType !== FormPanelType.new) {
      roleAssignmentQueryParams.filterSearchRA.roleDefinitionId = this.props.selectedRoleDefinition.id;
    } else {
      roleAssignmentQueryParams.filterSearchRA.roleDefinitionId = null;
    }
    roleAssignmentQueryParams.filterSearchRA.principalId = null;
    roleAssignmentQueryParams.skipToken = undefined;
    this.props.getListRAs(roleAssignmentQueryParams);
  }
}



