import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions } from '../../../models/AuthzActions';
import { IGraphObjectInfo } from '../../../models/graph/IGraphObjectInfo';
import { IRootAuthzState } from '../../../models/IRootAuthzState';
import { RoleDefinitionForm } from './RoleDefinitionForm';
import {
  IConnectedRoleDefinitionFormProps,
  IRoleDefinitionFormActions,
  IRoleDefinitionFormMappedProps
} from './RoleDefinitionForm.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the RoleDefinitionForm control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IRoleDefinitionFormProps) for the RoleDefinitionForm control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleDefinitionFormProps
): IRoleDefinitionFormMappedProps => {
  const { listGraphObjects } = state.app;
  return {
    listGraphObjects
    // selectedRoleDefinition
  };
};

/**
 * Maps the dispatch actions to the props for the RoleDefinitionForm control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props for the RoleDefinitionForm control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): IRoleDefinitionFormActions => {
  const getGraphObjectsAction = createAction<IGraphObjectInfo[]>(
    AuthzActions.getGraphObjectList,
    dispatch
  );
  return {
    getGraphObjectList: (graphObjectIds: IGraphObjectInfo[]) => {
      getGraphObjectsAction(graphObjectIds);
    }
  };
};

/**
 * A redux connected RoleDefinitionForm control.
 * @description Supports translation
 */
export const ConnectedRoleDefinitionForm = connect(
  mapStateToProps,
  mapDispatchToProps,
  null,
  { withRef: true }
)(translate('Role Definition Form', { withRef: true })(RoleDefinitionForm));



