import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../../models';
import { IRoleAssignmentQueryParams } from '../../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { IRoleDefinition } from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { RoleDefinitionPanel } from './RoleDefinitionPanel';
import {
  IConnectedRoleDefinitionPanelProps,
  IRoleDefinitionPanelActions,
  IRoleDefinitionPanelMappedProps
} from './RoleDefinitionPanel.type';
/**
 * Maps the external (i.e. own) props and the state to the properties of the RoleDefinition control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IRoleDefinitionProps) for the RoleDefinition control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleDefinitionPanelProps
): IRoleDefinitionPanelMappedProps => {
  const { selectedRoleDefinition, roleAssignmentQueryParams, currentNpd } = state.app;
  return {
    selectedRoleDefinition,
    roleAssignmentQueryParams,
    currentNpd
  };
};

/**
 * Maps the dispatch actions to the props for the RoleDefinition control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IRoleDefinitionProps) for the RoleDefinition control
 */
const mapDispatchToProps = (dispatch: Dispatch<IRootAuthzState>): IRoleDefinitionPanelActions => {
  const updateRoleDefinitionAction = createAction<IRoleDefinition>(AuthzActions.updateRoleDefinition, dispatch);
  const deleteRoleDefinitionAction = createAction<IRoleDefinition>(AuthzActions.deleteRoleDefinition, dispatch);
  const getRoleAssignmentsAction = createAction<IRoleAssignmentQueryParams>(
    AuthzActions.getRoleAssignmentList,
    dispatch
  );
  return {
    updateRoleDefinition: (roleDefinition: IRoleDefinition) => updateRoleDefinitionAction(roleDefinition),
    deleteRoleDefinition: (roleDefinition: IRoleDefinition) => deleteRoleDefinitionAction(roleDefinition),
    getListRAs: (queryRA: IRoleAssignmentQueryParams) => getRoleAssignmentsAction(queryRA)
  };
};

/**
 * A redux connected RoleDefinition control.
 * @description Supports translation
 */
export const ConnectedRoleDefinitionPanel = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('Role Definition Panel')(RoleDefinitionPanel));



