import { IColumn } from 'office-ui-fabric-react/lib/DetailsList';
import { IWithResponsiveModeState } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import { InjectedTranslateProps } from 'react-i18next';
import { IApplication } from '../../models/aadPAP/application/IApplication';
import { IRoleAssignmentQueryParams } from '../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { IListRDsState } from '../../models/aadPAP/roleDefinition/IRoleDefinition';
import { ISelectedRoleDefinition } from '../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { FormPanelType } from './RoleDefinitionPanel/RoleDefinitionPanel.type';

/**
 * Used as a standalone interface by mapStateToProps, the IRoleDefinitionMappedProps defines the non action properties.
 * for the RoleDefinition control.
 */
export interface IRoleDefinitionMappedProps {
  currentNpd: IApplication;
  listRoleDefinitions: IListRDsState;
  selectedRoleDefinition: ISelectedRoleDefinition;
  roleAssignmentQueryParams: IRoleAssignmentQueryParams;
}

/**
 * Used as a standalone interface by mapDispatchToProps, the IRoleDefinitionActions interface defines the actions
 * for the RoleDefinition control
 */
export interface IRoleDefinitionActions {
  getListRAs: (queryRA: IRoleAssignmentQueryParams) => void;
  getRoleDefinitionList(appId: string): void;
  setSelectedRoleDefinition(roleDefinition: ISelectedRoleDefinition): void;
  unsetSelectedRoleDefinition(): void;
}

/**
 * The props for the RoleDefinition control.
 */
export interface IRoleDefinitionProps
  extends IRoleDefinitionActions,
    IRoleDefinitionMappedProps,
    IConnectedRoleDefinitionProps,
    InjectedTranslateProps,
    IWithResponsiveModeState {}

/**
 * The props for the redux connected RoleDefinition
 * @description Called "own" props in the redux function mapStateToProps.
 * The mapStateToProps can map the properties of this interface to the IRoleDefinitionProps interface.
 */
export interface IConnectedRoleDefinitionProps {}

/**
 * Represents the RoleDefinition control's internal state
 */
export interface IRoleDefinitionState {
  showRDPanel: boolean;
  showRAPanel: boolean;
  showRAPanelType?: FormPanelType;
  formPanelType?: FormPanelType;
  selectedRoleDefinition: ISelectedRoleDefinition;
  listRoleDefinitions: IListRDsState;
  columns: IColumn[];
  // announced?: JSX.Element;
}

export enum AuthzPanelType {
  RoleDefinition = 'roleDefinition',
  RoleAssignment = 'roleAssignment'
}

export enum AssignedUserActionType {
  Clear = 'clear',
  Get = 'get'
}



