import * as Yup from 'yup';
import { IPermission } from '../../../models/aadPAP/roleDefinition/IPermission';
import { IRoleDefinition } from '../../../models/aadPAP/roleDefinition/IRoleDefinition';
import { IGraphObjectInfo } from '../../../models/graph/IGraphObjectInfo';
import { GUID_PATTERN } from '../../../utilities/guid';

export class RoleDefinitionFormValue {
  public id: string;
  public name: string = '';
  public description: string = '';
  public scopes: string[] = [''];
  public isBuiltIn: boolean = true;
  public createdBy: string = '';
  public createdOn: string = ''; // change to Date later
  public createdByPrincipal?: IGraphObjectInfo;
  public updatedBy: string = '';
  public updatedOn: string = '';
  public updatedByPrincipal?: IGraphObjectInfo;
  public permissions: IPermission[] = [
    {
      actions: [''],
      notActions: [],
      condition: ''
    }
  ];
  public isLoading: boolean = false;
  // tslint:disable-next-line:typedef
  public static validationSchema() {
    return Yup.object().shape({
      id: Yup.string()
        .required('Role Definition Id is required!')
        .matches(
          new RegExp(GUID_PATTERN),
          'Role Definition Id should be valid Guid'
        ),
      name: Yup.string().required('Role Definition Name is required!'),
      scopes: Yup.array().of(Yup.string().required('Scope can not be empty!')),
      permissions: Yup.array().of(
        Yup.object().shape({
          // actions:require first one string[], notAction:string[], condition: string
          actions: Yup.array().of(
            Yup.string().required('Action can not be empty!')
          ),
          notActions: Yup.array().of(
            Yup.string()
              .required('NotActions can not be empty!')
              .min(0)
          )
        })
      ),
      createdByPrincipal: Yup.string().notRequired(),
      updatedByPrincipal: Yup.string().notRequired()
    });
  }

  constructor(value: IRoleDefinition) {
    this.id = value.id || '';
    this.name = value.name || '';
    this.description = value.description || '';
    this.scopes = value.scopes;
    this.isBuiltIn = value.isBuiltIn || true;
    this.createdBy = value.createdBy || '';
    this.createdOn = value.createdOn || '';
    this.updatedBy = value.updatedBy || '';
    this.updatedOn = value.updatedOn || '';
    this.permissions = value.permissions;
  }
}



