import { IColumn } from 'office-ui-fabric-react/lib/DetailsList';
import { ResponsiveMode } from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { TranslationFunction } from 'react-i18next';
import { IRoleDefinition } from '../../models/aadPAP/roleDefinition/IRoleDefinition';

export interface IRoleDefinitionListColumn extends IColumn {
  fieldName: keyof IRoleDefinition;
  onRender?(item: IRoleDefinition): React.ReactNode;
}

export const getRoleDefinitionListColumns = (
  responsiveMode: ResponsiveMode,
  t: TranslationFunction
): IRoleDefinitionListColumn[] => {
  let columns: IRoleDefinitionListColumn[] = [
    {
      key: 'name',
      name: t('Role Definition Name'),
      fieldName: 'name',
      minWidth: 100,
      maxWidth: 200,
      className: 'ms-pii',
      isResizable: true
    }
  ];
  if (responsiveMode > ResponsiveMode.small) {
    columns.push({
      key: 'scopes',
      name: t('Scopes'),
      fieldName: 'scopes',
      minWidth: 100,
      maxWidth: 200,
      className: 'ms-pii',
      isResizable: true,
      onRender: item => <div>{item.scopes[0]}</div>
    });
  }
  if (responsiveMode > ResponsiveMode.large) {
    columns.push(
      {
        key: 'permissions',
        name: t(' Permissions'),
        fieldName: 'permissions',
        minWidth: 100,
        maxWidth: 400,
        className: 'ms-pii',
        isResizable: true,
        onRender: item => <div>{JSON.stringify(item.permissions)}</div>
      },
      {
        key: 'description',
        name: t('Description'),
        fieldName: 'description',
        minWidth: 100,
        maxWidth: 200,
        className: 'ms-pii',
        isResizable: true
      }
    );
  }
  return columns;
};



