import { css } from '@uifabric/utilities';
import { Field, FieldArray, FormikProps } from 'formik';
import { DefaultButton, IconButton } from 'office-ui-fabric-react/lib/Button';
import { Label } from 'office-ui-fabric-react/lib/Label';
import {
  Pivot,
  PivotItem,
  PivotLinkFormat
} from 'office-ui-fabric-react/lib/Pivot';
import * as React from 'react';
import { isNullOrUndefined, isUndefined } from 'util';

import { IPermission } from '../../../models/aadPAP/roleDefinition/IPermission';

import { ISelectedRoleDefinition } from '../../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { IGraphObjectInfo } from '../../../models/graph';
import { IBufferGraphObjects } from '../../../models/graph/IBufferGraphObjects';
import { getObjInfoFromBuffer } from '../../../redux/actions/graph/getObjInfoFromBuffer';
import { isGuid } from '../../../utilities/guid';
import Loading from '../../Common/Loading/Loading';
import { FormCheckbox } from '../../Forms/FormItem/FormCheckbox';
import { FormTextField } from '../../Forms/FormItem/FormTextField';
import { ConnectedRoleAssignmentsTable } from '../../RoleAssignment/RoleAssignmentsTable/ConnectedRoleAssignmentsTable';
import { AuthzPanelType } from '../RoleDefinition.types';
import { FormPanelType } from '../RoleDefinitionPanel/RoleDefinitionPanel.type';
import {
  IRoleDefinitionFormProps,
  IRoleDefinitionFormState
} from './RoleDefinitionForm.types';
import { RoleDefinitionFormValue } from './RoleDefinitionFormValue';

const styles = require('./RoleDefinitionForm.module.css');
const detailPanelStyles = require('../../../Styles/PanelList.scoped.scss');

export class RoleDefinitionForm extends React.Component<
  IRoleDefinitionFormProps,
  IRoleDefinitionFormState
> {
  constructor(props: IRoleDefinitionFormProps) {
    super(props);
    this.props.roleDefinitionFormikProps.resetForm(
      this._addCreateUpdateByPrincipal(
        this.props.roleDefinitionFormikProps.values,
        this.props.listGraphObjects.bufferGraphObjects
      )
    );
  }

  public componentWillReceiveProps(nextProps: IRoleDefinitionFormProps) {
    if (
      this.props.listGraphObjects.isLoading &&
      !nextProps.listGraphObjects.isLoading
    ) {
      if (
        isUndefined(
          this.props.roleDefinitionFormikProps.values.createdByPrincipal
        ) ||
        isUndefined(
          this.props.roleDefinitionFormikProps.values.updatedByPrincipal
        )
      ) {
        this.props.roleDefinitionFormikProps.resetForm(
          this._updateCreateUpdateByPrincipal(
            this.props.roleDefinitionFormikProps,
            nextProps.listGraphObjects.bufferGraphObjects
          )
        );
      }
    }
  }

  public render(): React.ReactNode {
    const { roleDefinitionFormikProps } = this.props;

    let form = (
      <div
        className={css(
          'ms-PanelFormContent',
          detailPanelStyles.panelFormBorder
        )}
      >
        <form
          onSubmit={roleDefinitionFormikProps.handleSubmit}
          className={css(
            'ms-PanelFormContent',
            detailPanelStyles.panelFormContent
          )}
        >
          <Pivot
            linkFormat={PivotLinkFormat.links}
            onLinkClick={this._onLinkClick}
          >
            <PivotItem
              headerText="Basic Properties"
              ariaLabel="Role Definition Basic Properties tab"
              className={css(detailPanelStyles.panelTabContent)}
            >
              {this._renderBasicPropertiesTab()}
            </PivotItem>
            <PivotItem
              headerText="Permissions"
              ariaLabel="Role Definition Permissions tab"
              className={css(detailPanelStyles.panelTabContent)}
            >
              {this._renderPermissionTab()}
            </PivotItem>
            {this._renderAssignmentsTab()}
          </Pivot>
        </form>
      </div>
    );

    return (
      <React.Fragment>
        <div
          data-automation-id="rolDefinitionContent"
          className="ms-DetailsContent"
        >
          {!this.props.roleDefinitionFormikProps.values.isLoading ? (
            <div
              className={css(
                'ms-DetailsContentCard',
                detailPanelStyles.detailsContentCard
              )}
            >
              <div className={css('ms-Grid', detailPanelStyles.cardFields)}>
                <div className="ms-Grid-row">{form}</div>
              </div>
            </div>
          ) : (
            <Loading />
          )}
        </div>
      </React.Fragment>
    );
  }

  private _removeOnClick = (
    // tslint:disable-next-line:no-any
    array: any[],
    index: number,
    field: string
  ) => {
    if (array.length <= 1) {
      return;
    }
    const { roleDefinitionFormikProps } = this.props;
    const n = [...array];
    n.splice(index, 1);
    roleDefinitionFormikProps.setFieldValue(field, n);
  }

  private _generateRemoveButton = (
    // tslint:disable-next-line:no-any
    valueArray: any[],
    index: number,
    field: string
  ) => {
    return (
      <IconButton
        iconProps={{ iconName: 'Delete' }}
        title="Delete"
        text="Delete"
        ariaLabel="Delete"
        onClick={() => this._removeOnClick(valueArray, index, field)}
        className={css(
          index === 0
            ? detailPanelStyles.scopeFormFirstIconButton
            : detailPanelStyles.scopeFormIconButton
        )}
      />
    );
  }

  private _generatePermissions = (
    permissions: IPermission[],
    isEditDisabled: boolean
  ): React.ReactNode => {
    // const { isEditDisabled } = this.props;
    return (
      <FieldArray
        name="permissions"
        // tslint:disable-next-line:jsx-no-lambda
        render={arrayHelpers => (
          <div>
            {permissions.map((_permission: IPermission, index) => (
              <div key={index}>
                <Label styles={{ root: styles.permissionLabel }}>
                  {'Permission ' + (index + 1)}
                </Label>
                <div
                  className={css(
                    'Permission' + (index + 1),
                    detailPanelStyles.permissionContent
                  )}
                >
                  <FieldArray
                    name={`permissions.${index}.actions`}
                    // tslint:disable-next-line:no-shadowed-variable
                    render={arrayHelpers => (
                      <div>
                        {permissions[index].actions.map(
                          (_action, actionIndex) => (
                            <div
                              key={`permissions.${index}.actions.${actionIndex}`}
                            >
                              <Field
                                name={`permissions.${index}.actions.${actionIndex}`}
                                component={FormTextField}
                                className={css(
                                  'Role-Definition-Permission-Item',
                                  detailPanelStyles.permissionFormItem
                                )}
                                label={actionIndex === 0 ? 'Actions' : ''}
                                ariaLabel={
                                  actionIndex === 0 ? 'Permissions Actions' : ''
                                }
                                required={actionIndex === 0 ? true : false}
                                disabled={isEditDisabled}
                              />
                              {isEditDisabled ||
                              permissions[index].actions.length <= 1 ? null : (
                                <IconButton
                                  iconProps={{ iconName: 'Delete' }}
                                  title="Delete"
                                  text="Delete"
                                  ariaLabel="Actions Delete"
                                  onClick={() =>
                                    permissions[index].actions.length > 1
                                      ? arrayHelpers.remove(actionIndex)
                                      : null
                                  }
                                  className={
                                    actionIndex === 0
                                      ? detailPanelStyles.permissionFormActionFirstDeleteIconButton
                                      : detailPanelStyles.permissionFormDeleteIconButton
                                  }
                                />
                              )}
                            </div>
                          )
                        )}
                        {isEditDisabled ? null : (
                          <IconButton
                            iconProps={{ iconName: 'Add' }}
                            title="Add"
                            text="Add"
                            ariaLabel="Actions Add"
                            onClick={() => arrayHelpers.push('')}
                            className={styles.permissionItemAddIconButton}
                          />
                        )}
                      </div>
                    )}
                  />
                  <FieldArray
                    name={`permissions.${index}.notActions`}
                    // tslint:disable-next-line:no-shadowed-variable
                    render={arrayHelpers => (
                      <div>
                        {permissions[index].notActions &&
                        permissions[index].notActions!.length === 0 ? (
                          this.props.roleDefinitionPanelType !==
                            FormPanelType.view || !isEditDisabled ? (
                            <Label>{'Not Actions '}</Label>
                          ) : (
                            <Field
                              name={' '}
                              component={FormTextField}
                              className={css(
                                'Role-Definition-Permission-Item',
                                detailPanelStyles.permissionFormItem
                              )}
                              label={'Not Actions'}
                              ariaLabel={'Not Actions'}
                              disabled={isEditDisabled}
                            />
                          )
                        ) : (
                          permissions[index].notActions!.map(
                            // tslint:disable-next-line:no-any
                            (_notAction: any, notActionIndex: number) => (
                              <div
                                key={`permissions.${index}.notActions.${notActionIndex}`}
                              >
                                <Field
                                  name={`permissions.${index}.notActions.${notActionIndex}`}
                                  component={FormTextField}
                                  className={css(
                                    'Role-Definition-Permission-Item',
                                    detailPanelStyles.permissionFormItem
                                  )}
                                  label={
                                    notActionIndex === 0 ? 'Not Actions' : ''
                                  }
                                  ariaLabel={`Not Actions ${notActionIndex}`}
                                  disabled={isEditDisabled}
                                />
                                {isEditDisabled ? null : (
                                  <IconButton
                                    iconProps={{ iconName: 'Delete' }}
                                    title="Delete"
                                    text="Delete"
                                    ariaLabel="Not Actions Delete"
                                    onClick={() =>
                                      arrayHelpers.remove(notActionIndex)
                                    }
                                    className={
                                      notActionIndex === 0
                                        ? detailPanelStyles.permissionFormNotActionFirstDeleteIconButton
                                        : detailPanelStyles.permissionFormDeleteIconButton
                                    }
                                  />
                                )}
                              </div>
                            )
                          )
                        )}
                        {isEditDisabled ? null : (
                          <IconButton
                            iconProps={{ iconName: 'Add' }}
                            title="Add"
                            text="Add"
                            ariaLabel="Not Actions Add"
                            onClick={() => arrayHelpers.push('')}
                            className={styles.permissionItemAddIconButton}
                          />
                        )}
                      </div>
                    )}
                  />
                  <Field
                    name={`permissions.${index}.condition`}
                    component={FormTextField}
                    className={css(
                      'Role-Definition-Permission-Item',
                      detailPanelStyles.permissionFormItem
                    )}
                    label={'Condition'}
                    ariaLabel="Condition"
                    value={permissions[index].condition || ''}
                    disabled={isEditDisabled}
                    multiline
                    autoAdjustHeight
                  />
                  {isEditDisabled || permissions.length <= 1 ? null : (
                    <div
                      className={css(detailPanelStyles.permissionFormAddButton)}
                    >
                      <DefaultButton
                        styles={{ root: css(styles.deletePermissionButton) }}
                        data-automation-id="delete-permission"
                        allowDisabledFocus={true}
                        disabled={false}
                        checked={false}
                        text="Delete This Permission"
                        ariaLabel="Delete This Permission Button"
                        iconProps={{ iconName: 'Delete' }}
                        onClick={() => {
                          permissions.length > 1
                            ? arrayHelpers.remove(index)
                            : null;
                        }}
                      />
                    </div>
                  )}
                </div>
              </div>
            ))}
            {isEditDisabled ? null : (
              <DefaultButton
                data-automation-id="add-permission"
                className={css(
                  'Permission-Form-Add-Button',
                  detailPanelStyles.permissionFormAddButton
                )}
                allowDisabledFocus={true}
                disabled={isEditDisabled}
                checked={false}
                text="Add Permission"
                ariaLabel="Add Permission Button"
                iconProps={{ iconName: 'Add' }}
                onClick={() => {
                  arrayHelpers.push({
                    actions: [''],
                    notActions: [],
                    condition: ''
                  });
                }}
              />
            )}
          </div>
        )}
      />
    );
  }

  private _renderBasicPropertiesTab = () => {
    const { roleDefinitionPanelType, isEditDisabled } = this.props;

    let roleDefinitionIdField,
      roleDefinitionNameField,
      roleDefinitionDescriptionField,
      roleDefinitionScopesField,
      roleDefinitionIsBuiltInField,
      createAndUpdateInfo = undefined;

    switch (roleDefinitionPanelType) {
      case FormPanelType.new:
        roleDefinitionIdField = this._getRoleDefinitionIdField();
        roleDefinitionNameField = this._getRoleDefinitionNameField(false);
        roleDefinitionDescriptionField = this._getRoleDefinitionDescriptionField(
          false
        );
        roleDefinitionScopesField = this._getRoleDefinitionScopesField(false);
        roleDefinitionIsBuiltInField = this._getRoleDefinitionIsBuiltInField();
        // createAndUpdateInfo = this._getCreateAndUpdateInfoField();
        break;
      case FormPanelType.edit:
      case FormPanelType.view:
      default:
        roleDefinitionIdField = this._getRoleDefinitionIdField();
        roleDefinitionNameField = this._getRoleDefinitionNameField(
          isEditDisabled
        );
        roleDefinitionDescriptionField = this._getRoleDefinitionDescriptionField(
          isEditDisabled
        );
        roleDefinitionScopesField = this._getRoleDefinitionScopesField(
          isEditDisabled
        );
        roleDefinitionIsBuiltInField = this._getRoleDefinitionIsBuiltInField();
        createAndUpdateInfo = this._getCreateAndUpdateInfoField();
        break;
    }

    const basicPropertiesTab: React.ReactNode = (
      <React.Fragment>
        {roleDefinitionIdField}
        {roleDefinitionNameField}
        {roleDefinitionDescriptionField}
        {roleDefinitionScopesField}
        {roleDefinitionIsBuiltInField}
        {createAndUpdateInfo}
      </React.Fragment>
    );

    return basicPropertiesTab;
  }

  private _renderPermissionTab = () => {
    const {
      roleDefinitionFormikProps,
      isEditDisabled,
      roleDefinitionPanelType
    } = this.props;

    return (
      <React.Fragment>
        {this._generatePermissions(
          roleDefinitionFormikProps.values.permissions,
          roleDefinitionPanelType === FormPanelType.new ? false : isEditDisabled
        )}
      </React.Fragment>
    );
  }

  private _renderAssignmentsTab = () => {
    const { roleDefinitionPanelType } = this.props;
    let assignmentsTab =
      !isUndefined(roleDefinitionPanelType) &&
      roleDefinitionPanelType === FormPanelType.new ? (
        <PivotItem />
      ) : (
        <PivotItem
          headerText="Assignments"
          ariaLabel="Role Assignments tab"
          className={css(detailPanelStyles.panelTabContent)}
        >
          <ConnectedRoleAssignmentsTable
            setShowPanel={this._toggleShowPanel}
            textFilter={''}
            isSmallTable={true}
          />
        </PivotItem>
      );
    return assignmentsTab;
  }

  private _toggleShowPanel = (showPanel: boolean, panelType: FormPanelType) => {
    this.props.togglePanel(
      AuthzPanelType.RoleDefinition,
      FormPanelType.view,
      false
    );
    this.props.togglePanel(AuthzPanelType.RoleAssignment, panelType, showPanel);
  }

  private _getRoleDefinitionIdField = () => {
    return (
      <Field
        name="id"
        label="Role Definition Id"
        arialLabel="Role Definition Id"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        required={true}
        disabled={true}
        component={FormTextField}
      />
    );
  }

  private _getRoleDefinitionNameField = (isEditDisabled: boolean) => {
    return (
      <Field
        name="name"
        label="Role Definition Name"
        arialLabel="Role Definition Name"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        type="name"
        required={true}
        disabled={isEditDisabled}
        component={FormTextField}
      />
    );
  }

  private _getRoleDefinitionDescriptionField = (isEditDisabled: boolean) => {
    return (
      <Field
        name="description"
        label="Description"
        arialLabel="Role Definition Description"
        className={css('Role-Definition-Form-Item', detailPanelStyles.formItem)}
        type="description"
        required={false}
        disabled={isEditDisabled}
        component={FormTextField}
      />
    );
  }

  private _getRoleDefinitionScopesField = (isEditDisabled: boolean) => {
    const { roleDefinitionFormikProps } = this.props;
    const roleDefinitionFormValues = this.props.roleDefinitionFormikProps
      .values;
    let scopesField = roleDefinitionFormValues.scopes.map(
      // tslint:disable-next-line:no-any
      (_v: any, i: number) => {
        const removeButton = this._generateRemoveButton(
          roleDefinitionFormValues.scopes,
          i,
          'scopes'
        );
        return (
          <div key={i}>
            <Field
              name={`scopes.${i}`}
              label={i === 0 ? 'Scopes' : ''}
              arialLabel={`Role Definition Scope ${i}`}
              type="scopes"
              required={i === 0 ? true : undefined}
              disabled={isEditDisabled}
              component={FormTextField}
              className={css(
                'Role-Definition-Scope-Item',
                i === 0
                  ? detailPanelStyles.scopeFormItemFirst
                  : detailPanelStyles.scopeFormItem
              )}
            />
            {/* {i === 0 || isEditDisabled ? null : removeButton} */}
            {isEditDisabled || roleDefinitionFormValues.scopes.length <= 1
              ? null
              : removeButton}
          </div>
        );
      }
    );

    let scopesAddButton = isEditDisabled ? null : (
      <div>
        <DefaultButton
          data-automation-id="add-scope"
          className={css(
            'Role-Definition-Scope-Add',
            detailPanelStyles.scopeFormAddButton
          )}
          allowDisabledFocus={true}
          disabled={false}
          checked={false}
          text="Add Scope"
          iconProps={{ iconName: 'Add' }}
          onClick={() => {
            roleDefinitionFormikProps.setFieldValue('scopes', [
              ...roleDefinitionFormikProps.values.scopes,
              ''
            ]);
          }}
        />
      </div>
    );
    return (
      <React.Fragment>
        {scopesField}
        {scopesAddButton}
      </React.Fragment>
    );
  }

  private _getRoleDefinitionIsBuiltInField = () => {
    return (
      <Field
        className={styles.checkBoxItem}
        name="isBuiltIn"
        label="Is Built-in"
        arialLabel={`isBuiltIn`}
        disabled={true}
        component={FormCheckbox}
      />
    );
  }

  private _getCreateAndUpdateInfoField = () => {
    const roleDefinitionFormValues = this.props.roleDefinitionFormikProps
      .values;
    return (
      <React.Fragment>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Created By')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {!isNullOrUndefined(roleDefinitionFormValues.createdByPrincipal) ? (
              <div
                className={'CreatedByPrincipal-Display-name'}
                // onChange={this._handleCreatedByPrincipalNameOnChange}
              >
                {roleDefinitionFormValues.createdByPrincipal.displayName} [
                {roleDefinitionFormValues.createdBy}]
              </div>
            ) : (
              roleDefinitionFormValues.createdBy
            )}
          </div>
        </div>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Created On')}
            {'(UTC)'}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {roleDefinitionFormValues.createdOn}
          </div>
        </div>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Updated By')}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {!isNullOrUndefined(roleDefinitionFormValues.updatedByPrincipal) ? (
              <div
                className={'UpdatedByPrincipal-Display-name'}
                // onChange={this._handleUpdatedByPrincipalNameOnChange}
              >
                {roleDefinitionFormValues.updatedByPrincipal.displayName} [
                {roleDefinitionFormValues.updatedBy}]
              </div>
            ) : (
              roleDefinitionFormValues.updatedBy
            )}
          </div>
        </div>
        <div className={css('ms-Grid-row ', detailPanelStyles.fieldItem)}>
          <div className={css('ms-FieldName', detailPanelStyles.fieldName)}>
            {this.props.t('Updated On')}
            {'(UTC)'}
          </div>
          <div className={css('ms-FieldValue', detailPanelStyles.fieldValue)}>
            {roleDefinitionFormValues.updatedOn}
          </div>
        </div>
      </React.Fragment>
    );
  }

  private _addCreateUpdateByPrincipal = (
    roleDefinition: ISelectedRoleDefinition,
    bufferGraphObjects: IBufferGraphObjects
  ): RoleDefinitionFormValue => {
    let roleDefinitionFormValue: RoleDefinitionFormValue = new RoleDefinitionFormValue(
      roleDefinition
    );

    let graphObjectIds: IGraphObjectInfo[] = [];
    if (isGuid(roleDefinition.createdBy)) {
      roleDefinitionFormValue.createdByPrincipal = getObjInfoFromBuffer(
        roleDefinition.createdBy!,
        bufferGraphObjects
      );
      if (
        isNullOrUndefined(roleDefinitionFormValue.createdByPrincipal) &&
        isGuid(roleDefinition.createdBy)
      ) {
        graphObjectIds.push({
          objectId: roleDefinition.createdBy
        } as IGraphObjectInfo);
      }
    }

    if (isGuid(roleDefinition.updatedBy)) {
      roleDefinitionFormValue.updatedByPrincipal = getObjInfoFromBuffer(
        roleDefinition.updatedBy!,
        bufferGraphObjects
      );
      if (
        isNullOrUndefined(roleDefinitionFormValue.updatedByPrincipal) &&
        isGuid(roleDefinition.updatedBy)
      ) {
        graphObjectIds.push({
          objectId: roleDefinition.updatedBy
        } as IGraphObjectInfo);
      }
    }

    if (graphObjectIds.length > 0) {
      this.props.getGraphObjectList(graphObjectIds);
    }

    return roleDefinitionFormValue;
  }

  private _updateCreateUpdateByPrincipal = (
    roleDefinitionFormikProps: FormikProps<RoleDefinitionFormValue>,
    bufferGraphObjects: IBufferGraphObjects
  ) => {
    const curRoleDefinitionFormValue = roleDefinitionFormikProps.values;
    const roleDefinitionFormValue: RoleDefinitionFormValue = this._addCreateUpdateByPrincipal(
      curRoleDefinitionFormValue,
      bufferGraphObjects
    );
    if (
      curRoleDefinitionFormValue.createdByPrincipal !==
        roleDefinitionFormValue.createdByPrincipal ||
      curRoleDefinitionFormValue.updatedByPrincipal !==
        roleDefinitionFormValue.updatedByPrincipal
    ) {
      roleDefinitionFormikProps.setFieldValue(
        'createdByPrincipal',
        roleDefinitionFormValue.createdByPrincipal
      );
      roleDefinitionFormikProps.setFieldValue(
        'updatedByPrincipal',
        roleDefinitionFormValue.updatedByPrincipal
      );
    }
    return roleDefinitionFormValue;
  }

  private _onLinkClick = (
    item?: PivotItem | undefined,
    _ev?: React.MouseEvent<HTMLElement> | undefined
  ): void => {
    if (item!.props.headerText === 'Assignments') {
      this.props.onChangeAssignedUsers();
    }
  }
}



