import { IContextualMenuItem } from 'office-ui-fabric-react/lib/components/ContextualMenu';
import { AuthzPanelType } from './RoleDefinition.types';
import { FormPanelType } from './RoleDefinitionPanel/RoleDefinitionPanel.type';
// import { TranslationFunction } from 'react-i18next';

// Getting initial CommandBar configured
export const getCommandBarItems = (
  refresh: () => void,
  setShowPanel: (panelType: AuthzPanelType, formPanelType: FormPanelType, showPanel: boolean) => void
): IContextualMenuItem[] => {
  return [
    {
      key: 'newItem',
      name: 'New',
      iconProps: {
        iconName: 'Add'
      },
      ariaLabel: 'New. Use left and right arrow keys to navigate',
      onClick: () => setShowPanel(AuthzPanelType.RoleDefinition, FormPanelType.new, true)
    },
    {
      key: 'refresh',
      name: 'Refresh',
      iconProps: {
        iconName: 'Refresh'
      },
      onClick: () => refresh()
    }
  ];
};



