import { LoadingState } from '@microsoft/portal-app/lib/models/ILoading';
import { CommandBar } from 'office-ui-fabric-react/lib/components/CommandBar';
import { Persona } from 'office-ui-fabric-react/lib/components/Persona';
import { TextField } from 'office-ui-fabric-react/lib/components/TextField';
import {
  CheckboxVisibility,
  DetailsList,
  DetailsListLayoutMode,
  IColumn,
  Selection
} from 'office-ui-fabric-react/lib/DetailsList';
import { FocusZone } from 'office-ui-fabric-react/lib/FocusZone';
import { css } from 'office-ui-fabric-react/lib/Utilities';
import {
  ResponsiveMode,
  withResponsiveMode
} from 'office-ui-fabric-react/lib/utilities/decorators/withResponsiveMode';
import * as React from 'react';
import { isNullOrUndefined, isUndefined } from 'util';
import {
  IListRDsState,
  IRoleDefinition
} from '../../models/aadPAP/roleDefinition/IRoleDefinition';
import { ISelectedRoleDefinition } from '../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { STYLES_AAD_LIST_ROLE_DEFS } from '../../models/aadPAP/roleDefinition/ITblAadListRDs';
import { STYLES_FILTER_DETAILS_LIST } from '../../Styles/STYLES_FILTER_QUERY_LIST_RAS';
import { copyAndSort } from '../../utilities/fabric';
import { renderEveryOtherRow } from '../../utilities/fabric/renderEveryOtherRow';
import { deepClone } from '../../utilities/object';
import Loading from '../Common/Loading/Loading';
import { ConnectedRoleAssignmentPanel } from '../RoleAssignment/RoleAssignmentPanel/ConnectedRoleAssignmentPanel';
import {
  AuthzPanelType,
  IRoleDefinitionProps,
  IRoleDefinitionState
} from './RoleDefinition.types';
import { getRoleDefinitionListColumns } from './RoleDefinitionList.columns';
import { getCommandBarItems } from './RoleDefinitionList.commands';
import { ConnectedRoleDefinitionPanel } from './RoleDefinitionPanel/ConnectedRoleDefinitionPanel';
import { FormPanelType } from './RoleDefinitionPanel/RoleDefinitionPanel.type';

const listPageStyles = require('@microsoft/portal-app/lib/styling/patterns/ListPage.scoped.scss');
const roleDefPageStyles = require('./RoleDefinition.scoped.scss');
const detailsPageStyles = require('@microsoft/portal-app/lib/styling/patterns/DetailsPage.scoped.scss');

/**
 * TODO: Describe what the RoleDefinition control represents.
 */
@withResponsiveMode
export class RoleDefinition extends React.Component<
  IRoleDefinitionProps,
  IRoleDefinitionState
> {
  private _selection: Selection;
  private pageFocusRef = React.createRef<HTMLDivElement>();

  constructor(nextProps: IRoleDefinitionProps) {
    super(nextProps);
    // TODO: Check this later Don't think you need put in constructor.
    this._selection = new Selection({
      onSelectionChanged: () => {
        const selectedItem = this._getSelectionDetails();
        if (!isUndefined(selectedItem)) {
          nextProps.setSelectedRoleDefinition({
            ...selectedItem,
            isLoading: true
          });
        }
        this.setState({
          selectedRoleDefinition: nextProps.selectedRoleDefinition
        });
      }
    });

    const responsiveMode = this._getResponsiveMode();
    let columns = getRoleDefinitionListColumns(responsiveMode, this.props.t!);

    this.state = {
      selectedRoleDefinition: {} as ISelectedRoleDefinition,
      listRoleDefinitions: {} as IListRDsState,
      showRDPanel: false,
      showRAPanel: false,
      columns: columns
      // announced: undefined
    };
  }

  public componentDidMount(): void {
    this.props.getRoleDefinitionList(this.props.currentNpd.appId);
    this._focus();
  }

  public componentWillReceiveProps(nextProps: IRoleDefinitionProps): void {
    if (
      !isNullOrUndefined(nextProps.listRoleDefinitions.roleDefinitions) &&
      nextProps.currentNpd.appId !== this.props.currentNpd.appId
    ) {
      this.props.getRoleDefinitionList(nextProps.currentNpd.appId);
      this.setState({
        listRoleDefinitions: deepClone(nextProps.listRoleDefinitions) /// can we combine all of these if together?
      });
    }

    if (
      (this.props.listRoleDefinitions.isLoading &&
        !nextProps.listRoleDefinitions.isLoading &&
        !isNullOrUndefined(nextProps.listRoleDefinitions.roleDefinitions)) ||
      (this.props.selectedRoleDefinition.isLoading &&
        !nextProps.selectedRoleDefinition.isLoading)
    ) {
      this.setState({
        listRoleDefinitions: deepClone(nextProps.listRoleDefinitions)
        // announced: <Announced message={'dream come true'} />
      });
    }
  }

  public render(): React.ReactNode {
    const { t } = this.props;
    // const { announced } = this.state;
    const responsiveMode = this._getResponsiveMode();
    let columns = this.state.columns;
    // command bar settings
    const commands = getCommandBarItems(this._refresh, this._setShowPanel);
    const listRoleDefinitions = this.state.listRoleDefinitions.roleDefinitions;

    let isHeaderVisible = true;
    let checkboxVisibility = CheckboxVisibility.hidden;
    if (responsiveMode <= ResponsiveMode.small) {
      isHeaderVisible = false;
      checkboxVisibility = CheckboxVisibility.hidden;
      columns = [
        {
          key: 'item',
          name: 'item',
          className: 'ms-pii',
          fieldName: 'name',
          minWidth: 1,
          onRender: this._renderCell
        }
      ];
    }
    // const numberOfResult = listRoleDefinitions ? listRoleDefinitions.length : 0;
    let list: React.ReactNode =
      this.props.listRoleDefinitions.isLoading &&
      this.props.listRoleDefinitions.loadingState === LoadingState.loading ? (
        <Loading />
      ) : (
        <React.Fragment>
          <DetailsList
            ariaLabelForGrid={this.props.t('Role Definition List')}
            isHeaderVisible={isHeaderVisible}
            checkboxVisibility={checkboxVisibility}
            items={listRoleDefinitions}
            columns={columns}
            setKey="objectId"
            selection={this._selection}
            selectionPreservedOnEmptyClick={true}
            styles={STYLES_AAD_LIST_ROLE_DEFS}
            layoutMode={DetailsListLayoutMode.justified}
            onRenderRow={renderEveryOtherRow}
            onColumnHeaderClick={this._onColumnClick}
            // onRenderMissingItem={this._onRenderMissingItem}
          />
        </React.Fragment>
      );

    return (
      <main
        data-automation-id="roleDefinitionPage"
        className={css('ms-ListPage', roleDefPageStyles.listPage)}
      >
        <div className={css('ms-ListPageHeader', listPageStyles.pageHeader)}>
          <div
            role="heading"
            tabIndex={-1}
            aria-level={1}
            ref={this.pageFocusRef}
            className={css(
              'ms-PageTitle',
              listPageStyles.pageTitle,
              roleDefPageStyles.pageTitle
            )}
          >
            {t('Role Definition')}
          </div>
        </div>
        <CommandBar
          items={commands}
          ariaLabel={this.props.t('Role Definition commands')}
        />
        <div
          className={css(
            'ms-DetailsContent',
            'ms-Grid',
            detailsPageStyles.detailsContent
          )}
        >
          <div className={css('Search-Bar-Row', 'ms-Grid-row')}>
            <TextField
              styles={STYLES_FILTER_DETAILS_LIST}
              onChange={this._onFilter}
              placeholder="Filter by name of role definition"
              ariaLabel="Filter by name of role definition"
            />
          </div>
          {/* {announced} */}
          <div className={css('Search-Bar-Row', 'ms-Grid-row')}>
            <FocusZone>{list}</FocusZone>
          </div>
        </div>

        <ConnectedRoleDefinitionPanel
          showPanel={this.state.showRDPanel}
          onDismiss={this._hideRDPanel}
          roleDefinitionPanelType={this.state.formPanelType!}
          showRAPanel={this._setShowPanel}
        />
        <ConnectedRoleAssignmentPanel
          showPanel={this.state.showRAPanel}
          roleAssignmentPanelType={this.state.formPanelType!}
          onDismiss={this._hideRAPanel}
        />
      </main>
    );
  }

  private _refresh = () => {
    if (this.props.currentNpd) {
      const appId = this.props.currentNpd.appId;
      this.props.getRoleDefinitionList(appId);
      this.setState({
        listRoleDefinitions: this.props.listRoleDefinitions,
        columns: this.state.columns.map(col => {
          col.isSorted = false;
          return col;
        })
      });
    }
  }

  private _focus = () => {
    const node = this.pageFocusRef.current;
    if (node) {
      node.focus();
    }
  }

  private _onColumnClick = (
    _event: React.MouseEvent<HTMLElement> | undefined,
    column: IColumn | undefined
  ): void => {
    let { listRoleDefinitions, columns } = this.state;
    let roleDefinitions = listRoleDefinitions.roleDefinitions;
    let isSortedDescending = column!.isSortedDescending;
    if (column!.isSorted) {
      isSortedDescending = !isSortedDescending;
    }

    if (column!.fieldName! === 'name') {
      let list = copyAndSort(
        roleDefinitions,
        column!.fieldName!,
        isSortedDescending
      );
      listRoleDefinitions.roleDefinitions = list;
      this.setState({
        listRoleDefinitions: listRoleDefinitions,
        columns: columns.map(col => {
          col.isSorted = col.key === column!.key;
          if (col.isSorted) {
            col.isSortedDescending = isSortedDescending;
          }
          return col;
        })
      });
    }
  }
  private _onFilter = (
    _ev: React.FormEvent<HTMLInputElement | HTMLTextAreaElement>,
    newValue?: string | undefined
  ): void => {
    const items = newValue
      ? this.props.listRoleDefinitions.roleDefinitions.filter(
          i => i.name.toLowerCase().indexOf(newValue) > -1
        )
      : this.props.listRoleDefinitions.roleDefinitions;

    const filterTbl: IListRDsState = {
      currentAppId: this.props.currentNpd.appId,
      roleDefinitions: items,
      hashIndexRDs: this.props.listRoleDefinitions.hashIndexRDs,
      isLoading: this.props.listRoleDefinitions.isLoading
    };

    this.setState({
      listRoleDefinitions: filterTbl
    });
  }

  private _getResponsiveMode = (): ResponsiveMode => {
    let { responsiveMode } = this.props;
    if (responsiveMode === undefined) {
      responsiveMode = ResponsiveMode.large;
    }
    return responsiveMode;
  }

  private _renderCell = (item: IRoleDefinition): React.ReactNode => {
    return (
      <div className="ms-CardListItem" data-is-focusable={true}>
        <Persona text={item.name} secondaryText={item.description} />
      </div>
    );
  }

  private _getSelectionDetails = (): IRoleDefinition => {
    const selectionItems = this._selection.getSelection();
    if (selectionItems.length !== 0) {
      this._setShowPanel(
        AuthzPanelType.RoleDefinition,
        FormPanelType.view,
        true
      );
    }
    const item = selectionItems[0] as IRoleDefinition;
    return item;
  }

  private _setShowPanel = (
    panelType: AuthzPanelType,
    formPanelType: FormPanelType,
    showPanel: boolean
  ): void => {
    switch (panelType) {
      case AuthzPanelType.RoleDefinition:
        this.setState({ showRDPanel: showPanel, formPanelType: formPanelType });
        break;
      case AuthzPanelType.RoleAssignment:
        this.setState({ showRAPanel: showPanel, formPanelType: formPanelType });
        break;

      default:
        break;
    }
  }

  private _hideRDPanel = (): void => {
    if (this.state.showRDPanel) {
      this._selection.toggleIndexSelected(
        this._selection!.getSelectedIndices()[0]
      );
      // this.props.unsetSelectedRoleDefinition();
    }
    this.setState({
      showRDPanel: false,
      selectedRoleDefinition: {} as ISelectedRoleDefinition
    });
  }

  private _hideRAPanel = (): void => {
    this.setState({
      showRAPanel: false
    });
  }
}



