import { mergeStyles } from '@uifabric/merge-styles';
import { IStyleFunctionOrObject } from '@uifabric/utilities';
import { IColumn, IDetailsListStyleProps, IDetailsListStyles } from 'office-ui-fabric-react/lib/DetailsList';
import { IRoleDefinition } from './IRoleDefinition';

export const STYLES_AAD_LIST_ROLE_DEFS: IStyleFunctionOrObject<IDetailsListStyleProps, IDetailsListStyles> = {
  root: mergeStyles({
    maxHeight: 'calc(100vh - 240px)'
  })
};

export interface ITblAadListRDs {
  appId: string;
  itemListRDs: IRoleDefinition[];
}

export const emptyTblListRDs: ITblAadListRDs = {
  appId: '',
  itemListRDs: []
};

export const COL_AAD_LIST_ROLE_DEFS: IColumn[] = [
  {
    key: 'name',
    name: 'Role Def Name',
    fieldName: 'name',
    headerClassName: 'tbl-header',
    minWidth: 80,
    maxWidth: 240,
    isResizable: true
  },
  {
    key: 'scopes',
    name: 'Scopes',
    fieldName: 'scopes0',
    headerClassName: 'tbl-header',
    minWidth: 80,
    maxWidth: 180,
    isResizable: true
  },
  {
    key: 'permissions',
    name: 'Permissions',
    fieldName: 'permissions',
    headerClassName: 'tbl-header',
    minWidth: 150,
    maxWidth: 600,
    isResizable: true
  },
  {
    key: 'description',
    name: 'Description',
    fieldName: 'description',
    headerClassName: 'tbl-header',
    minWidth: 100,
    maxWidth: 200,
    isResizable: true
  }
];

export const COL_EXPAND_AAD_LIST_ROLE_DEFS: IColumn[] = [
  {
    key: 'name',
    name: 'Role Def Name',
    fieldName: 'name',
    headerClassName: 'tbl-header',
    minWidth: 80,
    maxWidth: 240,
    isResizable: true
  }
];



