import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../models';
import { IRoleAssignmentQueryParams } from '../../models/aadPAP/roleAssignment/IRoleAssignmentQueryParams';
import { ISelectedRoleDefinition } from './../../models/aadPAP/roleDefinition/ISelectedRoleDefinition';
import { RoleDefinition } from './RoleDefinition';
import {
  IConnectedRoleDefinitionProps,
  IRoleDefinitionActions,
  IRoleDefinitionMappedProps
} from './RoleDefinition.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the RoleDefinition control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IRoleDefinitionProps) for the RoleDefinition control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedRoleDefinitionProps
): IRoleDefinitionMappedProps => {
  const {
    listRoleDefinitions,
    selectedRoleDefinition,
    currentNpd,
    roleAssignmentQueryParams
  } = state.app;
  return {
    listRoleDefinitions,
    selectedRoleDefinition,
    currentNpd,
    roleAssignmentQueryParams
  };
};

/**
 * Maps the dispatch actions to the props for the RoleDefinition control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IRoleDefinitionProps) for the RoleDefinition control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): IRoleDefinitionActions => {
  const getRoleDefinitionListAction = createAction<string>(
    AuthzActions.getRoleDefinitionList,
    dispatch
  );

  const setSelectedRoleDefinitionAction = createAction<ISelectedRoleDefinition>(
    AuthzActions.setSelectedRoleDefinition,
    dispatch
  );

  const unsetSelectedRoleDefinitionAction = createAction(
    AuthzActions.unsetSelectedRoleDefinition,
    dispatch
  );

  const getRoleAssignmentsAction = createAction<IRoleAssignmentQueryParams>(
    AuthzActions.getRoleAssignmentList,
    dispatch
  );

  return {
    getRoleDefinitionList: (appId: string) =>
      getRoleDefinitionListAction(appId),
    setSelectedRoleDefinition: (roleDefinition: ISelectedRoleDefinition) => {
      setSelectedRoleDefinitionAction(roleDefinition);
    },
    unsetSelectedRoleDefinition: () => unsetSelectedRoleDefinitionAction(),
    getListRAs: (queryRA: IRoleAssignmentQueryParams) =>
      getRoleAssignmentsAction(queryRA)
  };
};

/**
 * A redux connected RoleDefinition control.
 * @description Supports translation
 */
export const ConnectedRoleDefinition = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('OneAuthZ')(RoleDefinition));



